
import { defineComponent, ref } from 'vue'
export default defineComponent({
  name: 'Header',
  props: {
    addTodo: {
      type: Function,
      required: true
    }
  },
  setup(props) {
    const title = ref('')
    // 回车事件的回调函数，用来添加数据
    const add = () => {
      // 获取文本框中输入的数据，判断不为空
      const text = title.value
      if (!text.trim()) return
      // 此时有数据，创建一个todo对象
      const todo = {
        id: Date.now(),
        title: text,
        isCompleted: false
      }
      // 调用方法addTodo方法
      props.addTodo(todo)
      // 清空文本框
      title.value = ''
    }
    return { title, add }
  }
})
